'use client'

import React, { useState } from 'react'
import { useRouter } from 'next/navigation'
import { Rental } from '@/types/rental'
import { addRental } from '@/lib/storage'

const RentalForm: React.FC = () => {
  const router = useRouter()
  const [formData, setFormData] = useState({
    name: '',
    baseAmount: '',
    details: '',
    startDate: '',
    updateFrequencyValue: '3',
    updateFrequencyUnit: 'months' as 'days' | 'months',
    updateMethod: 'auto' as 'auto' | 'manual'
  })
  
  const [errors, setErrors] = useState<Record<string, string>>({})
  const [isSubmitting, setIsSubmitting] = useState(false)

  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {}

    if (!formData.name.trim()) {
      newErrors.name = 'El nombre es requerido'
    }

    if (!formData.baseAmount || parseFloat(formData.baseAmount) <= 0) {
      newErrors.baseAmount = 'El monto debe ser mayor a 0'
    }

    if (!formData.startDate) {
      newErrors.startDate = 'La fecha de inicio es requerida'
    } else {
      const startDate = new Date(formData.startDate)
      const today = new Date()
      if (startDate > today) {
        newErrors.startDate = 'La fecha de inicio no puede ser futura'
      }
    }

    if (!formData.updateFrequencyValue || parseInt(formData.updateFrequencyValue) <= 0) {
      newErrors.updateFrequencyValue = 'La frecuencia debe ser mayor a 0'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return

    setIsSubmitting(true)

    try {
      const newRental: Rental = {
        id: Date.now().toString(),
        name: formData.name.trim(),
        baseAmount: parseFloat(formData.baseAmount),
        currentAmount: parseFloat(formData.baseAmount),
        details: formData.details.trim() || undefined,
        startDate: formData.startDate,
        updateFrequency: {
          value: parseInt(formData.updateFrequencyValue),
          unit: formData.updateFrequencyUnit
        },
        updateMethod: formData.updateMethod,
        createdAt: new Date().toISOString(),
        lastUpdateDate: formData.startDate
      }

      addRental(newRental)
      router.push('/')
    } catch (error) {
      console.error('Error guardando alquiler:', error)
      setErrors({ submit: 'Error al guardar el alquiler. Intenta nuevamente.' })
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }))
    }
  }

  return (
    <div className="max-w-lg mx-auto bg-white rounded-lg shadow-sm border border-gray-200 p-6">
      <div className="mb-6">
        <h1 className="text-2xl font-bold text-gray-800 mb-2">Agregar Alquiler</h1>
        <p className="text-gray-600">Completa la informaci√≥n de tu propiedad</p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Nombre */}
        <div>
          <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-2">
            Nombre del Alquiler *
          </label>
          <input
            type="text"
            id="name"
            value={formData.name}
            onChange={(e) => handleInputChange('name', e.target.value)}
            className={`w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
              errors.name ? 'border-red-300' : 'border-gray-300'
            }`}
            placeholder="Ej. Terreno en el centro"
            disabled={isSubmitting}
          />
          {errors.name && <p className="mt-1 text-sm text-red-600">{errors.name}</p>}
        </div>

        {/* Monto Base */}
        <div>
          <label htmlFor="baseAmount" className="block text-sm font-medium text-gray-700 mb-2">
            Monto Base (ARS) *
          </label>
          <input
            type="number"
            id="baseAmount"
            value={formData.baseAmount}
            onChange={(e) => handleInputChange('baseAmount', e.target.value)}
            className={`w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
              errors.baseAmount ? 'border-red-300' : 'border-gray-300'
            }`}
            placeholder="1000000"
            min="0"
            step="1000"
            disabled={isSubmitting}
          />
          {errors.baseAmount && <p className="mt-1 text-sm text-red-600">{errors.baseAmount}</p>}
        </div>

        {/* Fecha de Inicio */}
        <div>
          <label htmlFor="startDate" className="block text-sm font-medium text-gray-700 mb-2">
            Fecha de Inicio *
          </label>
          <input
            type="date"
            id="startDate"
            value={formData.startDate}
            onChange={(e) => handleInputChange('startDate', e.target.value)}
            className={`w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
              errors.startDate ? 'border-red-300' : 'border-gray-300'
            }`}
            disabled={isSubmitting}
          />
          {errors.startDate && <p className="mt-1 text-sm text-red-600">{errors.startDate}</p>}
        </div>

        {/* Frecuencia de Actualizaci√≥n */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Frecuencia de Actualizaci√≥n *
          </label>
          <div className="flex space-x-3">
            <div className="flex-1">
              <input
                type="number"
                value={formData.updateFrequencyValue}
                onChange={(e) => handleInputChange('updateFrequencyValue', e.target.value)}
                className={`w-full px-3 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
                  errors.updateFrequencyValue ? 'border-red-300' : 'border-gray-300'
                }`}
                min="1"
                disabled={isSubmitting}
              />
            </div>
            <div className="flex-1">
              <select
                value={formData.updateFrequencyUnit}
                onChange={(e) => handleInputChange('updateFrequencyUnit', e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                disabled={isSubmitting}
              >
                <option value="months">Meses</option>
                <option value="days">D√≠as</option>
              </select>
            </div>
          </div>
          {errors.updateFrequencyValue && (
            <p className="mt-1 text-sm text-red-600">{errors.updateFrequencyValue}</p>
          )}
        </div>

        {/* Detalles */}
        <div>
          <label htmlFor="details" className="block text-sm font-medium text-gray-700 mb-2">
            Detalles (Opcional)
          </label>
          <textarea
            id="details"
            value={formData.details}
            onChange={(e) => handleInputChange('details', e.target.value)}
            rows={3}
            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            placeholder="Observaciones, ubicaci√≥n, inquilino, etc."
            disabled={isSubmitting}
          />
        </div>

        {/* Error de env√≠o */}
        {errors.submit && (
          <div className="p-3 bg-red-50 border border-red-200 rounded-md">
            <p className="text-sm text-red-600">{errors.submit}</p>
          </div>
        )}

        {/* Botones */}
        <div className="flex space-x-3 pt-4">
          <button
            type="button"
            onClick={() => router.push('/')}
            className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
            disabled={isSubmitting}
          >
            Cancelar
          </button>
          <button
            type="submit"
            className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            disabled={isSubmitting}
          >
            {isSubmitting ? 'Guardando...' : 'Guardar Alquiler'}
          </button>
        </div>
      </form>
    </div>
  )
}

export default RentalForm
